.button {
  background-color: hsl(180, 66%, 49%);
  color: white;
  border: none;
  border-radius: 20px;
  line-height: 2.5;
  padding: 0 1.5rem;
  font-weight: 700;
  font-family: Poppins;
  cursor: pointer;

  transition: ease-in-out 350ms;

  &:hover {
    background-color: hsl(180, 65%, 67%);
  }
}

.link {
  background-color: transparent;
  color: hsl(0, 0%, 75%);

  &:hover {
    color: hsl(257, 7%, 63%);
    background-color: transparent;
  }
}

// sizes
.small {
  line-height: 2.25;
  padding: 0 2.5rem;
}

.medium {
  line-height: 2.75;
  padding: 0 2.5rem;
}

.large {
  line-height: 3.5;
  padding: 0 2.5rem;
}

// variants
.square {
  border-radius: 5px;
}
