// Переменные
:root {
  /* Colors */
  --primary-100: #aaf1f1;
  --primary-300: hsl(180, 66%, 49%);
  --primary-600: hsl(257, 27%, 26%);
  --secondary-300: hsl(0, 87%, 67%);
  --gray-100: hsl(0, 0%, 75%);
  --gray-300: hsl(257, 7%, 63%);
  --gray-600: hsl(255, 11%, 22%);
  --gray-900: hsl(260, 8%, 14%);
  --bg-light: hsl(225, 33%, 95%);

  /* font */
  --family: "Poppins", sans-serif;
  --fw-normal: 500;
  --fw-bold: 700;

  --radii: 1rem;
  --radii-sm: 0.5rem;
}

/*NULLIFY Settings*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  font-family: "Poppins";
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/************************************************************/
#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;

  flex-direction: column;
}
.container {
  max-width: 1200px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;
}
main {
  flex: 1 1 100%;
}
