.CallToAction {
  background-image: url("../images/bg-boost-mobile.svg");
  background-color: var(--primary-600);
  background-size: cover;
  background-position: center;
  padding: 4rem 0;

  color: white;

  text-align: center;

  h2 {
    font-size: 24px;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 2rem;
  }

  @media (min-width: 1024px) {
    padding: 3rem 0;
    background-image: url("../images/bg-boost-desktop.svg");
  }
}
